h1.header {
  color: red;
  background-color: #333;
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  text-align: left;
  font-size: 6.8vw;
  margin: 0;
  overflow: hidden;
  position: relative;
  right: 6px;
  padding-top:1%;
}

ul.header {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  float: right;
}

li.header {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;

}

li.headeractive {
  display: inline-block;
  color: white;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  background-color: red;

}

img.header {
  float: left;
  border: 4px solid white;
  background-color: white;
  vertical-align: middle;
  position: absolute;
  left: 2%;
  top: 0;
  bottom: 0;
  margin: auto;
  max-width: 25%;
  z-index: 2;
}

div.header {
  background-color: #333;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  float: left;
  height: 18vw;
}

div.headerbottom {
  width: 75%;
  /* border: 4px solid green; */
  float: right;
}

li.header:hover {
  background-color: #111;
}

.active {
  background-color: red;
}

img.header {
  height: 78%;
  width: auto;
}



